import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

const routes = [
  {
    path: '*',
    redirect: '/home'
  },
  {
    name: 'home',
    component: () => import('./view/home'),
    meta: {
      title: '首页'
    }
  },
  {
    name: 'recharge/:anCode',
    component: () => import('./view/recharge/Recharge.vue'),
    meta: {
      title: '充值'
    }
  },
  {
    name: 'payment/:anCode',
    component: () => import('./view/recharge/Payment.vue'),
    meta: {
      title: '爱农充值'
    }
  },
  {
    name: 'withdraw/:anCode',
    component: () => import('./view/withdraw/Withdraw.vue'),
    meta: {
      title: '提现'
    }
  },
  {
    name: 'choosecard/:anCode',
    component: () => import('./view/withdraw/Choosecard.vue'),
    meta: {
      title: '选择账户'
    }
  },
  {
    name: 'passinfo/:anCode',
    component: () => import('./view/withdraw/Passinfo.vue'),
    meta: {
      title: '填写验证码'
    }
  },
  {
    name: 'resultsteps',
    component: () => import('./view/withdraw/Resultsteps.vue'),
    meta: {
      title: '结果详情'
    }
  }
];

// add route path
routes.forEach(route => {
  route.path = route.path || '/' + (route.name || '');
});

const router = new Router({ routes });

router.beforeEach((to, from, next) => {
  const title = to.meta && to.meta.title;
  
  /* start 判断主题 */
  let theme, themeStr, rtnTheme = ''
  let search = window.location.search
  if(search.includes('theme=')) {
    if (search.includes('&')) {
      theme = search.split('&')[1].split('=')[1]
      themeStr = [ ...theme ]
      themeStr.pop(themeStr.length-1)
      rtnTheme = themeStr.join('')
    } else {
      theme = search.split('=')[1]
      themeStr = [ ...theme ]
      themeStr.pop(themeStr.length-1)
      rtnTheme = themeStr.join('')
    }
  }
  to.meta.theme = rtnTheme || undefined
  /* end 判断主题 */

  if (title) {
    document.title = title;
  }
  next();
});

export {
  router
};
